<template>
	<section>
		<div class="zx-banner">
      <img src="../../../static/images/zx_banner2.jpg" alt="">
      <div class="zx-sqc">
        <span class="zx-sq-co clearfix"><em></em><p>申请开通专栏</p><em class="zx-sqce"></em></span>
        <span class="zx-sq-ctxt">随心写作，自由表达</span>
      </div>
    </div>
    
    <div class="inner clearfix">
        <!--<ul class="zx-myh-mzl">
          <li class="clearfix">
            <span><em>*</em>专栏名称</span>
            <input type="text" class="c-int">
          </li> 
          <li class="clearfix">
            <span><em>*</em>专栏专家</span>
            <input type="text" class="c-int">
          </li>
          <li class="clearfix">
            <span><em>*</em>作家介绍</span>
            <textarea class="c-txt"></textarea>
          </li>
          <li class="clearfix">
            <span><em>*</em>专栏图片</span>
            <span class="zx-myimg">
              <img src="../../../static/images/wl_bg14.jpg" alt="">
              <a href="" class="zx-myia">+</a>
              <p><em>注：</em>请上传一张封面图，尺寸160px*100px</p>
            </span>
          </li>
        </ul>
        <div class="zx-butt">
            <button type="button" class="zx-btn-bc">申请</button>
            <button type="button" class="zx-btn-bc">预览</button>
        </div>-->
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        	<el-form-item label="专栏名称" prop="name">
			    <el-input v-model.trim="ruleForm.name"></el-input>
			</el-form-item>
			<el-form-item label="专栏专家" prop="author">
			    <el-input v-model.trim="ruleForm.author"></el-input>
			</el-form-item>
			<el-form-item label="作家介绍" prop="authorInfo">
			    <el-input type="textarea" v-model.trim="ruleForm.authorInfo" ></el-input>
			</el-form-item>
			<el-form-item label="专栏图片" prop="picshow2">
				<ul class="zx-myh-mzl">
					<li>
						<span class="zx-myimg">
			              <img :src="picshow1" alt="" id="text-src" ref='show1'>
			              <a href="javascript:;" class="zx-myia" id="textPic">+</a>
			              <p><em>注：</em>请上传一张封面图，尺寸160px*100px</p>
			            </span>
					</li>
					 
	           </ul>
			</el-form-item>
			
			<el-form-item>
				
			    <el-button  @click="submitForm('ruleForm')">保存</el-button>
			    <el-button @click="preview('ruleForm')">预览</el-button>
			   
			</el-form-item>
        </el-form>
    </div> 

	</section>
</template>

<script>
	import {applacationZL,columnDetails,columnMy,previewColoumn} from '../../api/info';
	export default{
		data(){
			return{
				picshow1:'',
				 loading: false,
				id:'',
				ruleForm: {
			        name: '',
			       	author:'',
			       	authorInfo:'',
							picshow2:'qqww',
							 id:''
		        },
		         rules: {
		         	name: [
			            { required: true, message: '请输入专栏名称', trigger: 'blur' }
			        ],
			        author: [
			            { required: true, message: '请输入作者名', trigger: 'blur' } 
			        ],
			        authorInfo: [
			            { required: true, message: '请输入作者简介', trigger: 'blur' }
			        ],
			        picshow2: [
			            { required: true, message: '请上传图片', trigger: 'blur' }
			        ]
		         }
			}
		},
		methods:{
			resthtml(){
				 columnMy({}).then(res=>{
					 if(res.datas){
					 	 this.datas = res.datas;
						 this.ruleForm.name = res.datas.columnTitle;
						 this.ruleForm.author = res.datas.authorName;
						 this.ruleForm.authorInfo =  res.datas.columnBrief;
						 this.$refs.show1.src = res.datas.columnCover;
						  this.ruleForm.id = res.datas.id
					 }else{
					 	this.ruleForm.name = '';
						 this.ruleForm.author ='';
						 this.ruleForm.authorInfo =  '';
						 this.$refs.show1.src = '';
						  this.ruleForm.id = ''
					 }
					
				 })
			},
			submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		        	var url = this.$refs.show1.src;
		          if (valid) {		   
		          	if( this.ruleForm.id){
									var param={
		            	columnTitle:this.ruleForm.name,
		            	authorName:this.ruleForm.author,
		            	columnBrief:this.ruleForm.authorInfo,
									columnCover:url,
									id:	this.ruleForm.id
		            }
								}else{
									var param={
		            	columnTitle:this.ruleForm.name,
		            	authorName:this.ruleForm.author,
		            	columnBrief:this.ruleForm.authorInfo,
		            	columnCover:url	
		            }
								}
		            
		            
		            applacationZL(param).then(res=>{
		            	
		            	this.$message(res.message);
		            	this.$router.go(-1)
		            })
		          } else {
		            
		            return false;
		          }
		        });
		    },
		   
		    preview(formName){ 
		        this.$refs[formName].validate((valid) => {
		        	var url = this.$refs.show1.src;
		        	
			          if (valid) {	 	
			            if( this.ruleForm.id){
									var param={
		            	columnTitle:this.ruleForm.name,
		            	authorName:this.ruleForm.author,
		            	columnBrief:this.ruleForm.authorInfo,
									columnCover:url,
									id:	this.ruleForm.id
		            }
								}else{
									var param={
		            	columnTitle:this.ruleForm.name,
		            	authorName:this.ruleForm.author,
		            	columnBrief:this.ruleForm.authorInfo,
		            	columnCover:url	
		            }
								}
			            applacationZL(param).then(res=>{
			            	
//			            	this.id = res.datas.id
										console.log(res.code)
//			            	this.$router.push({path:'/preview',query:{id:this.id}})
									window.open(configURL.infoUrl+'/page/#/preview?id='+res.code+'',"_blank");
			            }) 
			          } else {
			            
			            return false;
			          }
			    });   
		    }
		},
		mounted(){
				this.resthtml()
			 picyulan('textPic',$('#text-src'),this.picshow1)
			
			function picyulan(btn,imgs,dataiten){
				var times = new Date().getTime()
				 //实例化一个plupload上传对象
    var uploader = new plupload.Uploader({
        browse_button:btn, //触发文件选择对话框的按钮，为那个元素id
        runtimes : 'html5,flash,silverlight,html4',
			
					//multi_selection: false,
				//container: document.getElementById('container'),
				flash_swf_url : '/static/upload/Moxie.swf',
				silverlight_xap_url : '/static/upload/Moxie.xap',
				url : 'http://oss.aliyuncs.com',
        filters: {
            //设置允许上传的类型
            max_file_size: '100mb', //设置允许上传的最大文件的大小
            mime_types: [
                //用来限定上传文件的类型,一个类型一个对象，放入当前数组
                {title: 'Image files', extensions: 'jpg,png,tif,bmp'}
            ]
        }
    });
    //在实例对象上调用init()方法进行初始化
    uploader.init()
    //当文件添加到上传队列后触发监听函数参数
    uploader.bind('FilesAdded', function (uploader, files) {
			var s = random_string();
			var geshi = get_suffix(files[0].name)
			var picname = s+geshi
			
			 var times = new Date().getTime()
				
		      	$.ajax({
		      		url:configUitl.getOssServerPath('','info')+ '&' +times,
		      		async:false,
		      		success:function(data){
								var data1 = {
			      					'key' : 'info/'+picname+'',
			                'policy': data.policy,
			                'OSSAccessKeyId': data.accessid,
			                'success_action_status' : '200', //让服务端返回200,不然，默认会返回204
			                //'callback' : null,
			                'signature': data.signature
			      		};
							//console.log(data)
							var picshow = data.host+'/info/'+picname;
							dataiten = picshow;
							
			      		 uploader.setOption({
									'url': data.host,
									'multipart_params':data1
							});
			      	
		      		}
		      		
		      	})
				uploader.start()
        //uploader为当前的plupload实例对象，files为一个数组，里面的元素为本次添加到上传队列里的文件对象
       // console.log(uploader,files);
        //队列添加文件后，直接调用文件上传，也可以单独拿出
       // uploader.start();
    });

  
    //当队列中的某一个文件上传完成后触发监听函数参数
    uploader.bind("FileUploaded",function (uploader,file,responseObject) {
        //uploader为当前的plupload实例对象，file为触发此事件的文件对象，responseObject为服务器返回的信息对象，它有以下3个属性：
        //response：服务器返回的文本
        //responseHeaders：服务器返回的头信息
				//status：服务器返回的http状态码，比如200
				imgs[0].src = dataiten;
       
    });     
		}
			
			function random_string(len) {
				len = len || 32;
				var chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';   
				var maxPos = chars.length;
				var pwd = '';
				for (var i = 0; i < len; i++) {
					pwd += chars.charAt(Math.floor(Math.random() * maxPos));
				}
			    return pwd;
			}
function get_suffix(filename) {
			     var pos = filename.lastIndexOf('.')
			    var suffix = ''
			    if (pos != -1) {
			        suffix = filename.substring(pos)
			    }
			    return suffix;
			}
			
		}
	}
</script>

<style>
	.c-page{
		display: none;
	}
	.myInfoTab{
		display: none;
	}
	.tabdiv{
		display: none;
	}
	.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 160px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 160px;
    height: 100px;
    display: block;
  }
  .el-form-item__label{
  	font-size: 18px !important;
  }
  .el-input__inner{
  	width: 47% !important;
  }
  .el-textarea__inner{
  	width: 67% !important;
  	height: 100px;
  }
  .demo-ruleForm .el-button{
  	padding: 15px 60px;
  	color: #fff;
  	background: #f5a623;
  	font-size: 18px;
  	margin-top: 30px;
  }
  .demo-ruleForm{
  	margin-bottom: 100px;
  	margin-top: 100px;
  	margin-left: 100px;
  }
</style>


